.call-to-action-section {
  &:before{
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    content: '';
    background: rgb(0 0 0 / 30%);
  }
  padding:180px 0 180px;  
  overflow: hidden;  
  .call-to-action-shape{
    position:absolute;   
    left:0;
    z-index:1;
    bottom:-20px;          
  }
  @include background-set(center, cover, no-repeat);

  @include media-breakpoint-down(lg) {
    padding:140px 0px;
  }
  .btn{
    font-size:2.5rem;
    padding: 24px 62px;
    @include media-breakpoint-down(lg) {
      font-size:1.6rem;
      padding: 10px 40px;      
    }    
  }
  .full-width-wysiwyg {
    margin-bottom: 20px;
    .editor-design {
      max-width: 100%;
      @include media-breakpoint-down(lg) {
        margin-bottom:0rem;          
      }
      h1{
        margin-bottom:4rem;
        @include media-breakpoint-down(lg) {
          margin-bottom:2rem;          
        }
      }
      p {
        font-size: 2.8rem;
        @include media-breakpoint-down(lg) {
          font-size: 1.6rem;          
        }
        br{
          display:none;
        }
      }
    }
  }
}
.book-appointment-section{
  padding:0;
  margin:70px 0 50px;
  @include media-breakpoint-down(lg) {
    margin: 50px 0;
  }
  @include media-breakpoint-down(md) {
    margin: 40px 0;
  }
}
.two-col-section + .iframe-section {
  margin-top: 50px;
  @include media-breakpoint-down(md) {
    margin-top:0;
  }
}

.comman-callto-action {
  max-width: 770px;
  margin: 0 auto;
  position: relative;
}