.teamlist-section {           
    .row{
        @include media-breakpoint-down(lg) {
            margin-bottom: -30px;
        }
    }
    .team-member {
        text-decoration: none;
        margin-bottom: 1rem;
        text-align: center;
        @include media-breakpoint-down(lg) {
            margin-bottom: 3rem;
        }
        .team-member-wrap {
            height: 100%;
            border-radius: 1.5rem;
            overflow: hidden;
            box-shadow: 2px 2px 14px rgb(0 0 0 / 10%);
            display: block;
            text-decoration: none;
            .member-img {
                width: 100%;
                height:300px;
                margin: 0 auto; 
                overflow: hidden;
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    @include transition(all 0.3s ease-in-out);
                    &:hover {
                        transform: scale(1.2);
                    }
                }
            }
            .member-details {
                padding: 4rem 2rem;
                word-break: break-all; 
                @include transition(0.3s ease-in-out);
                h4 {                   
                 font-weight:bold;
                 margin-bottom:15px;
                }
                h6{
                    font-weight:normal;
                    font-size:2rem;
                }
            }
        }
        &:last-child {
            @include media-breakpoint-down(md) {
                padding-bottom: 0;
            }
        }
    }
}


.member-details-section{    
    .member-img {
        padding-right: 50px;
        @include media-breakpoint-down(md) {
            padding-right: 0;
            margin-bottom:10px;
        }
        img{
            border-radius:1rem;
        }
    }
    ul.social-nav {
        display:flex;
        align-items:center;
        li{            
            padding-left:0;
            &:before{
                background: none;
            }
            margin: 0 5px;
            a{
                padding: 5px;                    
                font-size:23px;
                text-decoration: none;              
                img {
                max-width: 25px;
                margin-right:5px;
                }
            }
            &:before {
                display: none;
            }
        }
    }
    .col-md-12 {
        margin-top: 20px;
        @include media-breakpoint-down(lg) {
            margin-top:40px;            
        }
    }
}	

.get-in-touch{  
    &:before{
        position: absolute;
        content: '';
        top: 0;
        left: 50%;
        max-width: 1290px;
        height: 1px;
        transform: translate(-50%, -50%);                        
        width:95%;
        @include media-breakpoint-down(xl) {       
            width:90%;            
        }             
    }
    .location-wrap{        
        padding-right: 55px;        
        ul{
            margin-top:40px;       
            li {
                display: inline-flex;
                line-height:22px;
                margin-bottom: 20px;
                i {
                    margin-right: 11px;                   
                }
                a {
                    text-decoration: none;                  
                }
            }        
        }
    }
}