.newsletter-section{
    .row{
        align-items:center;
    }
    padding:90px 0;
    @include media-breakpoint-down(xl) {                    
        padding: 50px 0;
    }
    &:before{
        position: absolute;
        content: '';
        top: 0;
        left: 50%;
        max-width: 1290px;
        height: 1px;
        transform: translate(-50%, -50%);                        
        width: 100%;
        @include media-breakpoint-down(xl) {       
            width:90%;            
        }             
    }
    .shape-light-grey{
        position: absolute;
        bottom:0;
        left: 0;
        width:100%;
        z-index:-1;        
    }     
    .shape-color{
        top:0;
        width:100%;
        position: absolute;
        height:80%;   
        z-index:-1;
        @include media-breakpoint-down(md) {                    
            height:90%;           
        }
    }
    .full-width-wysiwyg{ 
        margin-bottom:0;
        padding-right:50px;
        @include media-breakpoint-down(md) {                    
            padding-right:0px;            
        }
        p{
            opacity:0.8;
        }
    }
    form#gform_3 {
        position: relative;        
        .gform_footer.top_label {
            position: absolute;
            top: 0;
            padding: 0 !important;
            margin: 0 !important;
            right: 0;
            @include media-breakpoint-down(lg) {                    
                position: unset;   
                margin-top:20px !important;            
            }
        }                     
    }   
}