.four-col-team-section {    
    .shape-light-grey{
        position: absolute;
        top: 0;
        left: 0;
        width:100%;
        z-index:-1;
    }
    .shape-color{
        height:90%;
        bottom:0;
        position: absolute;  
        width:100%;      
        z-index:-1;
    }
    .team-wrap {
        max-width:100%;             
        .team-member {
            padding-bottom: 20px;        
            @include media-breakpoint-down(lg) {                    
                padding-bottom:30px;  
            }
            .team-member-wrap{
                background:$white;
                height: 100%;
                border-radius:10px 10px 0 0; 
                box-shadow:0 12px 16px rgb(40 41 61 / 4%);
                .member-img {               
                    width: 100%;
                    height:300px;
                    margin: 0 auto;
                    overflow: hidden; 
                    border-radius:10px 10px 0 0;  
                    display:block;            
                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                        @include transition(all 0.3s ease-in-out);
                        &:hover{
                            transform:scale(1.2);
                        }
                    }
                }
                .member-details{
                    padding:2rem;                                       
                    p{
                        margin:20px 0;
                    }
                    a{
                        text-decoration: none;
                        font-weight: 600;
                        h4{
                            font-size:3.1rem;   
                            display: inline-block;
                            @include transition(all 0.3s ease-in-out);
                        }
                    }
                    .readmore{
                        margin-top: 10px;
                        display: inline-block;
                    }
                }
            }
            &:last-child{
                @include media-breakpoint-down(md) {                    
                    padding-bottom:0;  
                }                
            }
        }       
    }
    .btn{
        display: table;
        margin: 50px auto;
    }
}

.blog-listing-section {  
    .filter-wrap {
        text-align: center;   
        span{
            position: relative;            
            padding: 5px 10px;            
            border-radius: 10px;
            font-size: 16px;
            font-weight: 600;
            margin: 15px 0 25px;
            &:before{
                content:'\f107';
                font-family:'fontawesome';
                right: 20px;
                position: absolute;
            }
        }
        ul {
            display: flex;
            justify-content:center;
            flex-wrap:wrap;
            margin: 20px 0 60px;
            @include media-breakpoint-down(xl) {                    
             justify-content:center;
                margin: 20px 0 40px;
            }            
            @include media-breakpoint-down(md) {            
                display:none;
            }
            li {
                padding: 10px;
                border-radius: 8px;
                margin: 0;               
                line-height: 1;
                margin:0 10px;
                cursor: pointer;
                @include media-breakpoint-down(md) {
                    margin: 0;
                    width: 100%;
                    text-align: left;
                }               
            }
        }        
    }
    .btn{
        display: table;
        margin: 40px auto 0;
    }
}
