.top-nav {
  .client-area-wrap {
    flex-wrap: wrap;
    order: 3;
    ul {
      padding:0;
      min-width: 260px;      
      border:0;
      li {
        margin: 0;
        a {           
          font-weight: 500;
          padding:7px 10px;
          margin-bottom: 0;
          white-space: normal;
          display: block;
          transition: all .3s ease-in-out;
        }
      }
    }
    .dropdown-toggle::after {
      display: none;
    }
  }

  .container {
    display: flex;
    justify-content: space-between;
    padding: 8px 0;
    position: relative;

    @include media-breakpoint-down(md) {
      padding: 8px 15px;
    }
  }

  ul {
    margin-left: 10px;
    @include media-breakpoint-down(sm) {
      margin-left: 0;
    }
    li {
      margin: 0 2px;      
      a {
        padding: 5px;
        @include media-breakpoint-down(md) {
          padding: 5px 0;         
        }
        font-size: 15px;
        text-decoration: none;
        img {
          max-width: 20px;
          margin-right: 5px;
          @include transition(all 0.4s ease-in-out);
        }
      }
    }
  }

  .social-nav {
    ul li a {
      font-size: 23px;
    }
  }

  .header-contact {
    display: flex;
    @include media-breakpoint-down(md) {
      flex-wrap: wrap;
    }
    ul {
      display: flex;

      @include media-breakpoint-down(lg) {
        display: flex;
        flex-direction: column;
      }

      li a {
        i {
          font-size: 18px;
          margin-right: 6px;
        }

        &:hover {
          text-decoration: underline;
        }
      }
    }

    .social-nav {
      @include media-breakpoint-down(md) {
        position: absolute;
        right: 0;
        bottom: 5px;
      }      
      ul {
        flex-direction:row;
        li {
          a {

          }
        }
      }
    }
  }
}

header {
  @include media-breakpoint-down(xl) {
    .btn {
      min-width: 170px;
    }
  }

  @include transition(all 0.3s ease-in-out);

  &.fixed {
    position: fixed;
    background: $white;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    padding: 0;
    @include shadow(-1px 1px 7px 0px rgba(0, 0, 0, 0.12));
    z-index: 99;
  }
}

.navbar-dark {
  .navbar-nav {
    .menu-item-has-children {
      &.nav-item {
        margin-right: 15px;
      }
    }

    .nav-item {
      margin: 0 5px;
      padding: 0 5px;

      @media(max-width:1400px) {
        margin: 0 3px;
        padding: 0 3px;
      }

      @include media-breakpoint-down(lg) {
        margin: 0 12px;
      }

      .nav-link {
        font-size: 1.8rem;
        line-height: 1.5rem;
        font-weight: $font-weight-normal;
        @include transition(all 0.3s ease-in-out);

        @media(max-width:1400px) {
          font-size: 1.5rem;
        }

        @include media-breakpoint-down(lg) {
          padding: 15px 0;
        }
      }

      &:hover {

        .nav-link {

          &::after {}
        }
      }
    }

    @include media-breakpoint-up(lg) {
      .menu-item-has-children {
        &:hover {
          .dropdown-menu {
            display: block
          }
        }
      }
    }

    .menu-item-has-children {
      .arrow-icon {
        display: none;

        @include media-breakpoint-down(lg) {
          display: block;

          &:after {
            border: 0;
            font-family: fontawesome;
            content: "\f107";
            position: absolute;
            right: -5px;
            font-size: 18px;
            top: 10px;
            @include transition(all 0.2s ease-in-out);
          }

          &.arrow-flip {
            &:after {
              transform: rotate(-180deg);
            }
          }
        }
      }
    }
  }

  .btn {
    margin-left: 30px;

    @include media-breakpoint-down(xl) {
      padding: 6px 15px;
      font-size: 1.6rem;
      margin: 10px 0 0 15px;
    }
  }

  img {
    @media(max-width:1400px) {
      max-width: 150px;
    }
  }

  .dropdown-menu {
    @include shadow(-1px 1px 7px 3px rgba(0, 0, 0, 0.12));
    border: 0;
    font-size: 14px;
    width: 180px;
    padding: 0;

    @include media-breakpoint-down(lg) {
      box-shadow: unset;
      width: 100%;
      border-radius: 0;
      border-left: 0;
      border-right: 0;
      margin: 10px 0;
    }

    .nav-item {
      margin: 0px;
      padding: 0;

      &.menu-item-has-children {
        a {
          &:after {
            display: block;
            content: "\f105";
            font-family: fontawesome;
            position: absolute;
            right: 15px;
            top: 50%;
            transform: translateY(-50%);

            @include media-breakpoint-down(lg) {
              display: none;
            }
          }
        }

        .dropdown-menu {
          position: absolute;
          left: 100%;
          top: 0;

          @include media-breakpoint-down(lg) {
            position: relative;
            left: 0;
            padding-left: 15px;
          }

          a {
            &:after {
              display: none;
            }
          }
        }
      }

      .dropdown-item {
        font-weight: $font-weight-medium;
        padding: 13px 15px;

        @include media-breakpoint-down(lg) {
          padding: 7px 15px;
        }

        margin-bottom:0px;
        white-space: normal;

        &:hover {
          @include transition(all 0.3s ease-in-out);
        }
      }

      &:first-child .dropdown-item {
        &:hover {
          border-top-left-radius: 2.5px;
          border-top-right-radius: 2.5px;
        }
      }

      &:last-child .dropdown-item {
        &:hover {
          border-bottom-left-radius: 2.5px;
          border-bottom-right-radius: 2.5px;
        }
      }
    }
  }



  .dropdown-toggle::after {
    border: 0;
    font-family: 'fontawesome';
    content: '\f107';
    position: absolute;
    @include transition(all 0.3s ease-in-out);
    right: -5px;
    font-size: 17px;

    @include media-breakpoint-down(lg) {
      display: none;
    }
  }

  .dropdown-toggle.show::after {
    @include media-breakpoint-down(lg) {
      transform: rotate(180deg);
    }
  }

  .navbar-toggler {
    border: 0;

    .navbar-toggler-icon {
      position: relative;
      width: 24px;
      background: transparent;


      &::before,
      &::after {
        position: absolute;
        width: 100%;
        height: 2px;
        border-radius: 30px;
        content: '';
        left: 0;
      }

      &::after {
        width: 70%;
        top: 8px;
        right: 0;
        left: unset;
      }
    }
  }

  .offcanvas {
    border: 0;

    .offcanvas-header {
      .btn-close-white {
        opacity: 1;
        filter: unset;
        margin: 10px;
      }
    }
  }
}

.navbar {
  padding: 30px 0 30px;

  @include media-breakpoint-down(lg) {
    padding: 20px 0 20px;

    img {
      max-width: 150px;
    }
  }

  .navbar-collapse {
    @include media-breakpoint-down(lg) {
      position: fixed;
      top: 152px;
      right: -100%;
      height: 100vh;
      z-index: 11;
      width: 350px;
      padding: 20px 15px 140px 15px;
      @include transition(all 0.3s ease-in-out);
      display: block;
      overflow-y: auto;
    }
  }
}

.menu-open {
  overflow: hidden;

  .navbar {
    .navbar-collapse {
      right: 0;
    }

    .navbar-toggler {
      .navbar-toggler-icon {
        &:before {
          transform: rotate(-45deg);
        }

        &:after {
          top: 0;
          width: 100%;
          transform: rotate(45deg);
        }
      }
    }
  }
}